import React, {ChangeEvent, FC, FormEvent, useRef, useState} from "react";
import {RouteComponentProps, withRouter} from "react-router-dom"



const SearchBlock:FC<RouteComponentProps> = (props) => {

    const [searchInput, setSearchInput] = useState("");

    const inputField = useRef<HTMLInputElement>(null!);

    const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
        setSearchInput(e.target.value);

    }

    const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
        e.preventDefault();

        setSearchInput("");
        inputField.current.blur();
        return props.history.push("/under-construction")
    };

    // const handleClick = (e) => {
    //     e.target.blur();
    // }

    return (
        <div className="search-block">
            <div className="d-flex">
                <form onSubmit={handleSubmit} className="d-flex justify-content-center align-items-center ">
                    <input type="text" ref={inputField} onChange={handleChange} placeholder="Enter your search phrase"
                           value={searchInput}/>
                    <button className="btn btn-outline-secondary">
                        <svg
                            width="1em"
                            height="1em"
                            viewBox="0 0 16 16"
                            className="bi bi-search"
                            fill="currentColor"
                            xmlns="http://www.w3.org/2000/svg"
                        >
                            <path
                                fillRule="evenodd"
                                d="M10.442 10.442a1 1 0 0 1 1.415 0l3.85 3.85a1 1 0 0 1-1.414 1.415l-3.85-3.85a1 1 0 0 1 0-1.415z"
                            />
                            <path
                                fillRule="evenodd"
                                d="M6.5 12a5.5 5.5 0 1 0 0-11 5.5 5.5 0 0 0 0 11zM13 6.5a6.5 6.5 0 1 1-13 0 6.5 6.5 0 0 1 13 0z"
                            />
                        </svg>
                    </button>
                </form>
            </div>
        </div>
    );
};

export default withRouter(SearchBlock);
